/* Question.css */

.question-container {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 86.5%;
  width: 100%;
  border-radius: 20px 20px 0 0;
  background-color: var(--primary-background-color);
  padding: 32px;
  box-sizing: border-box;
}

.options {
  width: 100%;
}

.option-item {
  display: flex;
  position: relative;
  background-color: var(--inputfield-background-color);
  margin: 5px 0;
  padding: 10px;
  padding-left: 30px;
  border-radius: 16px;
  width: 100%;
  height: 52px;
  box-sizing: border-box;
  cursor: pointer;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
}

.option-item input {
  opacity: 0;
  cursor: pointer;
}

.option-input {
  width: 28px;
  height: 28px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  position: absolute;
  top: 12px;
  left: 12px;
  background-color: var(--white);
  border-radius: 50%;
}

.option-item:has(> input:checked) {
  background-color: var(--button-color);
  color: var(--secondary-text-color);
  font-weight: 700;
}

.check {
  position: absolute;
  top: 20px;
  left: 17px;
  display: none;
}

.option-item input:checked ~ .check {
  display: block;
}

.submit-button {
  background-color: var(--button-color);
  color: var(--secondary-text-color);
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  display: block; /* Make the button block to occupy the full width */
  width: 100%;
  margin-top: 20px;
}

.submit-button:hover {
  background-color: #357abd;
}

.form p {
  margin: 0;
}

.company-info {
  padding-top: 3vh;
  padding-bottom: 3vh;
  box-sizing: border-box;
}

/* header */

.question-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  padding: 0px 24px 0px 24px;
  align-items: center;
  gap: 8px;
  height: 13.5%;
  align-items: end;
  box-sizing: border-box;
}

.question-header-items {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
}

.points-button-wrapper {
  margin-left: auto;
}

/* Results page little tweaks */
.results-form-title {
  display: flex;
  justify-content: space-between;
}

.option-item-results {
  display: flex;
  align-items: center;
  background-color: var(--inputfield-background-color);
  margin: 5px 0;
  border-radius: 16px;
  width: 100%;
  height: 52px;
  box-sizing: border-box;
  padding: 10px;
  justify-content: space-between;
  position: relative;
}

.option-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #1bc82c33;
  border-radius: 16px 0 0 16px;
}

.redirect-message {
  text-align: center;
  margin-top: 20px;
}

.redirect-bar-container {
  width: 100%;
  height: 20px;
  background-color: var(--inputfield-background-color);
  border-radius: 16px;
  overflow: hidden;
  margin-top: 10px;
}

.redirect-bar {
  height: 100%;
  width: 0;
  background-color: var(--button-color);
  animation: fillRedirectBar 10s linear forwards;
}

@keyframes fillRedirectBar {
  to {
    width: 100%;
  }
}
